<template lang='pug'>
.main-app
  video.screen-lock(ref='screenLock', src='res/white.mp4', loop, muted)
  h1 NoBrowserTimer
  .quote-container
    .quote {{quote}}

  circular-progress-bar(
    backgroundColor='#f3f3f3',
    foregroundColor='#a1df8b',
    :outerRadius='45',
    :innerRadius='35',
    :currentProgress='(imageShownTime | 0) / 3600',
    :caption='sec2Time(imageShownTime)',
    @select='onSVGSelectAngle'
  )

  .checkboxes
    input(type='checkbox', id='useSound', v-model='useSound', :disabled='isRunning')
    label(for='useSound') Use sound

    input(type='checkbox', id='allowHiddenTab', v-model='allowHiddenTab', :disabled='isRunning')
    label(for='allowHiddenTab') Allow hidden tab

  button.button-start(v-if='!isRunning', @click='issueStart') Start timer
  button.button-stop(v-if='isRunning', @click='issueInterrupt') Interrupt timer

  before-unload-handler(message='Really quit?', v-if='isRunning')

</template>

<script>

import {
  getCurrentTime,
  sec2Time,
  xorshift32
} from '@/utils'

import CircularProgressBar from '@/components/CircularProgressBar'
import BeforeUnloadHandler from '@/components/BeforeUnloadHandler'

import { isActiveTab } from '@/visibility'
import quotes from '@/quotes'

function justPlay (audioElement) {
  audioElement.pause()
  audioElement.currentTime = 0
  audioElement.play()
}

export default {
  name: 'MainApp',
  components: {
    CircularProgressBar,
    BeforeUnloadHandler
  },
  data () {
    const currentTime = getCurrentTime()
    return {
      isRunning: false,
      timerStartTime: null,
      currentTime,
      duration: 15 * 60,
      offStartTime: null,

      useSound: true,
      allowHiddenTab: false
    }
  },
  computed: {
    quote () {
      const t = xorshift32(this.currentTime / 60 | 0) % quotes.length
      return quotes[t]
    },
    imageShownTime () {
      if (this.isRunning) {
        const remainingTime = this.timerStartTime + this.duration - this.currentTime
        return Math.max(0, remainingTime)
      } else {
        return this.duration
      }
    },
    timerElapsedTime () {
      if (this.isRunning) {
        return this.currentTime - this.timerStartTime
      } else {
        return 0
      }
    },
    isActiveTab () {
      return isActiveTab.value
    }
  },
  mounted () {
    this.timerUpdater = setInterval(this.onInterval, 500)
    this.bellSound = new Audio('res/bell.mp3')
    this.alertSound = new Audio('res/alert.mp3')
    this.resumeSound = new Audio('res/resume.mp3')
    this.resumeSound.volume = 0.6
    this.interruptSound = new Audio('res/interrupt.mp3')
    this.interruptSound.volume = 0.5
  },
  beforeDestroy () {
    clearInterval(this.timerUpdater)
  },
  methods: {
    sec2Time,

    onInterval () {
      this.currentTime = getCurrentTime()
      if (!this.isActiveTab && this.isRunning) {
        if (this.offStartTime && getCurrentTime() - this.offStartTime >= 15) {
          this.onInterrupt()
        }
      }
    },

    onSVGSelectAngle (angle) {
      if (this.isRunning) return
      const newSec = (angle / 3 + 0.5 | 0) * 30
      this.duration = newSec
    },

    issueStart () {
      this.$notify({
        group: 'timer',
        title: 'Starting timer',
        text: `Duration: ${sec2Time(this.duration)}`
      })
      this.timerStartTime = getCurrentTime()
      this.isRunning = true
      this.offStartTime = null
      this.$refs.screenLock.play()
      justPlay(this.resumeSound)
    },

    stopTimer () {
      this.timerStartTime = null
      this.isRunning = false
      this.$refs.screenLock.pause()
    },

    issueInterrupt () {
      if (confirm('Really interrupt?')) {
        this.onInterrupt()
      }
    },

    onInterrupt () {
      if (!this.isRunning) return

      new Notification('Timer interuptted') // eslint-disable-line no-new
      this.$notify({
        group: 'timer',
        type: 'warn',
        title: `Timer interrupted. Elapsed: ${sec2Time(this.timerElapsedTime)}`
      })
      this.stopTimer()
      if (this.useSound) justPlay(this.interruptSound)
    },

    onEnd () {
      new Notification(`Timer done: ${sec2Time(this.duration)}`) // eslint-disable-line no-new
      this.$notify({
        group: 'timer',
        title: 'Timer done!',
        text: `Duration: ${sec2Time(this.duration)}`
      })
      this.stopTimer()
      if (this.useSound) justPlay(this.bellSound)
    }
  },
  watch: {
    imageShownTime (v) {
      if (this.isRunning && v === 0) {
        this.onEnd()
      }
    },
    isActiveTab (v) {
      if (this.isRunning) {
        if (this.allowHiddenTab) return

        if (v === false) {
          const n = new Notification('Come back within 15 seconds to preserve timer!')
          setTimeout(() => n.close(), 6000)
          this.offStartTime = getCurrentTime()
          if (this.useSound) justPlay(this.alertSound)
        } else {
          const n = new Notification('Glad you\'re back')
          setTimeout(() => n.close(), 5000)
          this.offStartTime = null
          if (this.useSound) {
            this.alertSound.pause()
            justPlay(this.resumeSound)
          }
        }
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.main-app {
  flex: 1;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  user-select: none;

  h1 {
    @media screen and (max-width: 400px) {
      font-size: 2em;
    }

    @media screen and (min-width: 400px) and (max-width: 500px) {
      font-size: 2.5em;
    }

    @media screen and (min-width: 500px) {
      font-size: 3em;
    }

  }

  .button-start, .button-stop {
    width: 80vw;
    max-width: 40rem;
    padding: 15px;
    margin: 15px 0 0 0;

    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5em;
    border: none;
    -webkit-appearance: none;
  }

  .button-start {
    background-color: #b2ee9a;
  }

  .button-stop {
    background-color: #ffbdb4;
  }

  .quote-container {
    height: 10rem;
    overflow-y: auto;
    @media screen and (max-width: 600px) {
        font-size: 1em;
    }

    @media screen and (min-width: 600px) {
        font-size: 1.5em;
    }
    width: 80vw;
    max-width: 50rem;

    .quote {
      text-align: justify;
      font-style: italic;
      border-left: 6px solid #888;
      padding: 5px 20px 5px 20px;
    }
  }

  .checkboxes {
    padding: 5px;

    input[type='checkbox'] {
      padding-left: .5em;
    }
    label {
      padding-right: .5em;
    }
  }

  .screen-lock {
    position: fixed;
    left: 0;
    top: 0;
  }
}
</style>
