<template lang="pug">
svg.progress(:viewBox='computeViewBox', @mousedown='onSVGInteraction' @mousemove='onSVGInteraction' @mouseup='onSVGInteraction')
  path(:d='getArcPath(360)', :fill='backgroundColor', fill-rule='evenodd')
  path(:d='getArcPath(currentProgress * 360)', :fill='foregroundColor', fill-rule='evenodd')
  text.text(x=50, y=50, dominant-baseline='middle') {{caption}}
</template>

<script lang="js">
import Vue from 'vue'

function polarToCartesian (centerX, centerY, radius, angleInDegrees) {
  var angleInRadians = (angleInDegrees - 90) * Math.PI / 180.0

  return {
    x: centerX + (radius * Math.cos(angleInRadians)),
    y: centerY + (radius * Math.sin(angleInRadians))
  }
}

export default Vue.extend({
  props: {
    backgroundColor: {
      type: String,
      default: '#f3f3f3'
    },
    foregroundColor: {
      type: String,
      default: '#a1df8b'
    },
    currentProgress: {
      type: Number,
      default: 0.5
    },
    outerRadius: {
      type: Number,
      default: 45
    },
    innerRadius: {
      type: Number,
      default: 35
    },
    paddingRadius: {
      type: Number,
      default: 5
    },
    caption: {
      type: String,
      default: ''
    }
  },
  computed: {
    computeViewBox () {
      const viewBoxSize = this.paddingRadius + this.outerRadius
      return `0 0 ${viewBoxSize * 2} ${viewBoxSize * 2}`
    }
  },
  methods: {
    onSVGInteraction (e) {
      if (e.type === 'mousemove' && e.buttons === 0) return

      const { clientX: clickedClientX, clientY: clickedClientY } = e
      const { left, top, width, height } = this.$el.getBoundingClientRect()
      const translatedX = (clickedClientX - left) / width * 100 - 50
      const translatedY = (clickedClientY - top) / height * 100 - 50
      const distFromCenter = Math.sqrt(translatedX * translatedX + translatedY * translatedY)
      if (this.innerRadius <= distFromCenter && distFromCenter <= this.outerRadius) {
        let angle = Math.atan2(translatedX, -translatedY) / Math.PI * 180
        if (angle < 0) angle += 360
        this.$emit('select', angle)
        const newSec = (angle / 3 + 0.5 | 0) * 30
        this.duration = newSec
      }
    },

    getArcPath (angle) {
      const r1 = this.outerRadius
      const r2 = this.innerRadius

      if (angle > 360) angle = 360
      if (angle < 0) angle = 0
      if (angle === 360) {
        return [
          'M', 50, 50 - r1,
          'A', r1, r1, 0, 0, 0, 50, 50 + r1,
          'A', r1, r1, 0, 0, 0, 50, 50 - r1,
          'M', 50, 50 - r2,
          'A', r2, r2, 0, 0, 0, 50, 50 + r2,
          'A', r2, r2, 0, 0, 0, 50, 50 - r2
        ].join(' ')
      }

      const start1 = { x: 50, y: 50 - r1 }
      const end1 = polarToCartesian(50, 50, r1, angle)
      const start2 = polarToCartesian(50, 50, r2, angle)
      const end2 = { x: 50, y: 50 - r2 }
      const isBigArc = (angle >= 180) ? 1 : 0

      return [
        'M', start1.x, start1.y,
        'A', r1, r1, 0, isBigArc, 1, end1.x, end1.y,
        'L', start2.x, start2.y,
        'A', r2, r2, 0, isBigArc, 0, end2.x, end2.y,
        'L', start1.x, start1.y
      ].join(' ')
    }
  }
})
</script>

<style lang="scss" scoped>

.progress {
  margin: 10px;
  max-height: 60%;
  text {
    font: bold 15px sans-serif;
    text-anchor: middle;
  }
}

</style>
