import { sprintf } from 'sprintf-js'

export function getCurrentTime () {
  return (new Date().getTime()) / 1000
}

export function sec2Time (sec) {
  sec = (sec + 0.5) | 0
  return sprintf('%02d:%02d', (sec / 60) | 0, sec % 60)
}

export function xorshift32 (x) {
  x = (x ^ (x << 13)) & 0xFFFFFFFF
  x = (x ^ (x >>> 17)) & 0xFFFFFFFF
  x = (x ^ (x << 5)) & 0xFFFFFFFF
  if (x < 0) x += 0x100000000
  return x
}
