<template lang='pug'>
#app
  notifications(group='timer')
  span(v-if='!notificationGranted')
    p Allow notification to run this app
    button(@click='grantNotification') Grant notification permission
    button(@click='notificationGranted = true') Skip
  MainApp(v-else)

</template>

<script>

import MainApp from './components/MainApp'

export default {
  name: 'App',
  data () {
    return {
      notificationGranted: (Notification.permission === 'granted')
    }
  },
  components: {
    MainApp
  },
  methods: {
    grantNotification () {
      Notification.requestPermission().then(this.onNotificationPermission)
    },
    onNotificationPermission (permission) {
      if (permission === 'granted') {
        this.notificationGranted = true
      }
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 20px 0;
  flex: 1;

  display: flex;
}

body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
    display: flex;
}

</style>
